/* My styling */

#main-sec {
  padding: 30px 20px;
  text-align: center;
}
.heading {
  color: rgb(75, 74, 74);
}
hr {
  width: 60px;
  height: 2px;
  background-color: rgb(75, 74, 74);
  margin: -10px auto 60px;
}
.input {
  border: 2px dashed rgb(234, 153, 234);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 35px;
  padding: 40px;
  box-sizing: border-box;
  width: 95%;
  max-width: 400px;
  border-radius: 20px;
}
.label {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 100%;
  border-radius: 20px;
  background-color: rgba(227, 130, 227, 0.246);
  cursor: pointer;
  transition: all 0.2s linear;
}
.label:hover {
  background-color: rgba(248, 15, 248, 0.246);
}
.label img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.files {
  width: 95%;
  max-width: 400px;
  margin: 10px auto 50px;
  text-align: left;
}
.files .list-item {
  display: flex;
  align-items: center;
  padding: 8px 20px;
  margin-bottom: 7px;
  border-radius: 10px;
  width: 100%;
  background-color: rgba(128, 228, 233, 0.194);
  border: 1px solid rgba(246, 206, 246, 0.309);
  text-align: left;
  transition: all 0.2s linear;
}
.list-item:hover {
  background-color: rgba(38, 235, 245, 0.608);
}
.files .list-item img {
  width: 22px;
  height: 22px;
  margin-right: 8px;
}
.player {
  padding: 50px 10px 20px;
  box-sizing: border-box;
  border-radius: 20px;
  background-color: rgba(231, 230, 230, 0.869);
  width: 95%;
  max-width: 400px;
  margin: 10px auto 50px;
}
.player h5 {
  max-width: 200px;
  margin: 10px auto 35px;
}
.player audio {
  width: 90%;
}
